.sign-user-container {
  background-repeat: no-repeat;
  display: flex;
  width: 100%;
}

.sign-user {
  width: 25%;
  border-radius: 3px;
  border-left: 1px solid rgb(244, 244, 244);
  padding: 5px 20px;
}

.sign-img-container {
  width: 70%;
  display: flex;
  justify-content: center;
}

.login-img {
  /* width: 40%; */
  /* height: 60%; */
}

@media screen and (max-width: 850px) {
  .sign-user-container {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
  }

  .sign-img-container {
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .sign-user {
    width: 90%;
}


  .login-img {
    width: 40%;
  }
}

